.Planets {
  text-align: center;
  box-shadow: 0 0 20px 1px rgba(0,0,255,0.2), inset 0 0 5px 1px rgba(0,0,255,0.3);
  border-radius: 5px;
  padding: 20px;
  margin: 20px;
}

.Planets {
  ul {
    list-style-type: none;
    padding: 0;
    margin: 20px auto 20px;
    min-width: 900px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.Planets {
  ul {
    li {
      padding: 10px;
      margin: 10px;
      border-radius: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}

.Planets {
  button {
    display: block;
    outline: none;
    background: #fff;
    color: #555;
    font-weight: bold;
    border: none;
    padding: 10px 30px;
    cursor: pointer;
    box-shadow: 0 0 1px 1px rgba(0,0,0,0.2);
    border-radius: 5px;
  }
}

.Planets {
  button:hover {
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.2);
  }
}

.Planets {
  button:active {
    box-shadow: 0 0 10px 1px rgba(0,0,255,0.5);
  }
}

.Planets {
  input {
    display: block;
    outline: none;
    display: block;
    width: 50%;
    padding: 10px 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
}

.Search {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.BackwardForward {
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.Logout {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.Logout {
  button {
    border-radius: 20px;
  }
}

.Finished {
  animation: finished 0.1s 6 forwards, colorchange 5s forwards;
}

@keyframes finished {
  25% {transform: translateX(-3px)}
  75% {transform: translateX(3px)}
}

@keyframes colorchange {
  0% {box-shadow: 0 0 10px 1px rgba(255,0,0,0.5);}
}

.User {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 100px;
}

.Timer {
  display: block;
  width: 60px;
  height: 60px;
  line-height: 60px;
  background-color: #fff;
  color: #fcc;
  font-size: 40px;
  font-weight: bold;
}

.Wait {
  color: #fcc;
  font-size: 20px;
  font-weight: bold;
}