.LoginContainer {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Login {
    width: 400px;
    margin: 0 auto;
    padding: 20px;
    box-shadow: 0 0 10px 1px rgba(0,0,255,0.5);
    border-radius: 5px;
}

.Login {
    input, button {
        display: block;
        outline: none;
    }
}

.Login {
    input {
        margin-bottom: 20px;
        display: block;
        width: 100%;
        padding: 10px 20px;
        border: 1px solid #ccc;
        border-radius: 5px;
    }
}

.Login {
    button {
        background: #fff;
        color: #555;
        font-weight: bold;
        border: none;
        padding: 10px 30px;
        cursor: pointer;
        box-shadow: 0 0 1px 1px rgba(0,0,0,0.2);
        border-radius: 20px;
    }
}

.Login {
    button:hover {
        box-shadow: 0 0 10px 1px rgba(0,0,0,0.2);
    }
}

.Login {
    button:active {
        box-shadow: 0 0 10px 1px rgba(0,0,255,0.5);
    }
}

.LoginError {
    animation: loginerror 0.1s 6 forwards, colorchange 5s forwards;
}

@keyframes loginerror {
    25% {transform: translateX(-5px);}
    75% {transform: translateX(5px);}
}

@keyframes colorchange {
    0% {box-shadow: 0 0 10px 1px rgba(255,0,0,0.5);}
}

.Loader {
    display: flex;
    justify-content: center;
    align-items: center;
}