:root {
    --size: 30px;
}

.loader {
    width: calc(var(--size) * 4);
    display: flex;
    justify-content: space-around;
}

.ball {
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    box-shadow: 0 0 5px 1px rgba(0,0,255,0.5);
    animation: bounce 1.4s infinite ease-in-out both;
}

.ball:nth-of-type(1) {
    animation-delay: -0.48s;
}
.ball:nth-of-type(2) {
    animation-delay: -0.32s;
}
.ball:nth-of-type(3) {
    animation-delay: -0.16s;
}

@keyframes bounce {
	0%, 80%, 100% {
		transform: scale(0);
	}

	40% {
		transform: scale(1.0);
	}
}